import React from 'react';
import { useLeagueStore } from '../../store/league';
import { useUserStore } from '../../store/user';
import { TableWrapper } from './LeaguesTable.styled';
import { DialogModal } from '../Dialog/Dialog';
import { EditLeagueModal } from '../EditLeagueModal/EditLeagueModal';

const LeaguesTable = () => {
    const [isModalOpen, setIsModalOpen] = React.useState(false);
    const [selectedLeague, setSelectedLeague] = React.useState(null);
    const user = useUserStore((state) => state.user);
    const { leagues, getLeagues } = useLeagueStore((state) => {
        return {
            leagues: state.leagues,
            getLeagues: state.getLeagues,
        };
    });

    React.useEffect(() => {
        if (user) {
            getLeagues(user);
        }
    }, [user, getLeagues]);

    function handleRowClick(league) {
        setSelectedLeague(league);
        setIsModalOpen(true);
    }

    function closeModal() {
        setIsModalOpen(false);
    }

    return (
        <>
            <TableWrapper>
                <table>
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Platform</th>
                            <th>League Id</th>
                            <th>Consolation Games</th>
                        </tr>
                    </thead>
                    <tbody>
                        {leagues.map((league, i) => {
                            return (
                                <tr key={i} onClick={() => handleRowClick(league)}>
                                    <td>{league.name}</td>
                                    <td>{league.platform}</td>
                                    <td>{league.leagueid}</td>
                                    <td>{league.consolationGames ? 'Yes' : 'No'}</td>
                                </tr>
                            );
                        })}
                    </tbody>
                </table>
            </TableWrapper>
            <DialogModal isOpened={isModalOpen} onClose={closeModal}>
                {selectedLeague && (
                    <EditLeagueModal league={selectedLeague} handleClose={closeModal} />
                )}
            </DialogModal>
        </>
    );
};

export default LeaguesTable;
