import React from 'react';
import { useLeagueStore } from '../../store/league';

export const EditLeagueModal = ({ league, handleClose }) => {
    const { removeLeague, updateLeague } = useLeagueStore((state) => {
        return {
            removeLeague: state.removeLeague,
            updateLeague: state.updateLeague,
        };
    });

    async function handleDeleteLeague() {
        await removeLeague(league.id);
        handleClose();
    }

    async function handleUpdateLeague(e) {
        e.preventDefault();

        const elementsArray = [...e.target.elements];
        const formData = elementsArray.reduce((acc, elem) => {
            if (!!elem.id) {
                if (elem.type === 'checkbox') {
                    acc[elem.id] = elem.checked;
                } else {
                    acc[elem.id] = elem.value;
                }
            }
            return acc;
        }, {});

        await updateLeague(league.id, formData);
        handleClose();
    }

    return (
        <div>
            <h2>Edit League Info</h2>
            <small>
                Currently Editing {league.platform}-{league.leagueid}
            </small>

            <form
                className="base-form"
                onSubmit={handleUpdateLeague}
                key={`${league.platform}-${league.leagueid}`}
            >
                <div>
                    <label htmlFor="name" className="base-label">
                        Name
                    </label>
                    <input
                        className="base-input"
                        type="text"
                        defaultValue={league.name}
                        required
                        placeholder="Name"
                        id="name"
                        name="name"
                    />
                </div>
                <div className="form-row">
                    <input
                        id="consolationGames"
                        name="consolationGames"
                        type="checkbox"
                        defaultChecked={league.consolationGames}
                    />
                    <label htmlFor="consolationGames" className="base-label base-label--horizontal">
                        Consolation Games
                    </label>
                </div>
                <button type="submit">Submit</button>
                <button
                    type="button"
                    className="base-button base-button--danger"
                    onClick={handleDeleteLeague}
                >
                    Delete League
                </button>
            </form>
        </div>
    );
};
