import { getFirebase } from '../lib/firebase';
import create from 'zustand';
import { useUserStore } from './user';

const firebase = getFirebase();
const db = firebase.firestore();

export const useLeagueStore = create((set, get) => ({
    leagues: [],
    loading: false,
    getLeagues: async (user) => {
        const { email } = user;
        const leagues = await db.collection('users').where('email', '==', email).get();

        set({
            leagues: leagues.docs.map((league) => {
                return {
                    id: league.id,
                    ...league.data(),
                };
            }),
        });
    },
    removeLeague: async (leagueId) => {
        await db.collection('users').doc(leagueId).delete();
        set({
            leagues: get().leagues.filter((league) => league.id !== leagueId),
        });
    },
    updateLeague: async (leagueId, data) => {
        await db.collection('users').doc(leagueId).update(data);
        set({
            leagues: get().leagues.map((league) => {
                if (league.id === leagueId) {
                    return { ...league, ...data };
                } else {
                    return league;
                }
            }),
        });
    },
}));

useUserStore.subscribe(
    (state) => state.user,
    (user) => {
        if (user) {
            useLeagueStore.getState().getLeagues(user);
        } else {
            useLeagueStore.setState({ leagues: [] });
        }
    }
);
