import styled from 'styled-components';
import { themeGet } from '@styled-system/theme-get';

export const TableWrapper = styled.div`
    max-width: 1440px;
    padding: 1rem;
    margin: auto;

    table {
        border-collapse: collapse;
    }

    table tr th {
        background: #222;
        color: #fff;
        padding: 5px;
        border: #111 solid 1px;
        text-align: left;
        font-weight: bold;
    }
    table tr td {
        border: 1px #ccc solid;
        padding: 5px;
    }

    table tr td.rightright {
        background: #eee;
    }
    table tr td h4 {
        margin: 0;
        padding: 0;
        font-weight: normal;
    }

    table {
        margin-bottom: 2em;
        width: 100%;
    }
    th {
        border-bottom: 2px solid #ccc;
        font-weight: bold;
        text-align: left;
    }
    td {
        border-bottom: 1px solid #ddd;
    }
    caption,
    th,
    td {
        padding: 4px 10px 4px 0;
    }
    caption {
        background: #f1f1f1;
        padding: 10px 0;
        margin-bottom: 1em;
    }

    tr,
    td,
    th {
        vertical-align: middle;
    }

    /* Use this if you use span-x classes on th/td. */
    table .last {
        padding-right: 0;
    }

    tbody tr {
        cursor: pointer;
    }
`;
